.slot-machine-root {
  display: flex;
}

.slot-machine-root button {
  cursor: pointer;
  position: relative;
  margin: 30px;
  height: 100px;
  width: 100px;
  align-self: center;
  background-color: var(--primary);
  border-radius: 100%;
  color: var(--primaryText);
  border: none;
  font-size: 2.5em;
  font-weight: bold;
  box-shadow: 1px 1px 10px var(--secondary);
}

.slot-machine-root button:focus {
  outline: none;
}

.slot-machine-root button:active {
  box-shadow: none;
}

.slot-machine-root button:active:before {
  display: none;
}

.slot-machine-root button:before {
  content: "";
  position: absolute;
  border-radius: 100%;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(
    150deg,
    rgba(255, 255, 255, 0.2),
    rgba(255, 255, 255, 0.05)
  );
}

.slot-machine-root .col {
  display: flex;
  flex-direction: column;
  width: 100px;
  height: 337px;
  overflow: hidden;
}

.slot-machine-root img.sprite {
  height: 110px;
  width: 100px;
  object-fit: none;
  transform: translateY(100px);
  border-radius: 5px;
}

@keyframes transition-to-0 {
  15% {
    transform: translateY(var(--spriteOffset7));
    animation-timing-function: ease-in;
  }
  25% {
    transform: translateY(var(--spriteOffset0));
    animation-timing-function: linear;
  }
  35% {
    transform: translateY(var(--spriteOffset7));
    animation-timing-function: linear;
  }
  40% {
    transform: translateY(var(--spriteOffset0));
    animation-timing-function: linear;
  }
  50% {
    transform: translateY(var(--spriteOffset7));
    animation-timing-function: linear;
  }
  60% {
    transform: translateY(var(--spriteOffset0));
    animation-timing-function: linear;
  }
  75% {
    transform: translateY(var(--spriteOffset7));
    animation-timing-function: ease-out;
  }
  100% {
    transform: translateY(var(--spriteOffset0));
    animation-timing-function: ease;
  }
}

